import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-work" data-section="timelinework">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3">
                {/* <span className="heading-meta">Timeline</span> */}
                <h2 className="colorlib-heading">Work Experience</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry">
                    <div className="timeline-entry-inner">
                      {/* <div className="timeline-icon color-3">
                        <i className="icon-briefcase" />
                      </div> */}
                      <div className="timeline-label">
                        <h2>Internship at ALARGO SYSTEMS <br></br><span>Sep 2020 - Nov 2020</span></h2>
                        <p>
                          Joined ALARGO SYSTEMS as a Software/Web Developer-Intern. My primary task was to find ways to improve the Inventory Management of the Company. 
                          I developed an android app to help the company's employees identify product info on the go and perform actions to keep the company's ERP system stock values updated. 
                          </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry">
                    <div className="timeline-entry-inner">
                      {/* <div className="timeline-icon color-3">
                        <i className="icon-briefcase" />
                      </div> */}
                      <div className="timeline-label">
                        <h2>Internship at Heraklion Chamber of Commerce and Industry <br></br><span>Oct 2016 - Mar 2017</span></h2>
                        <p>
                          Completed my internship there as a requirement for my Bachelor of Business Administration degree. I was tasked with keeping the Business Registry up to date and having the responsibility to issue certificates. 
                        </p>
                      </div>
                    </div>
                  </article>
                  {/* <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article> */}
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
