import React, { Component } from 'react'

export default class About extends Component {

  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }


  render() {
    const { status } = this.state;
    return (
      <div>
        <section className="colorlib-contact" data-section="contact">
        <div className="colorlib-narrow-content">
          <div className="row">
          <div className="col-md-12">
            <div className="row row-bottom-padded-sm">
            <div className="col-md-12">
                <div className="about-desc">
                  <h2 className="colorlib-heading">Contact</h2>
                  <div className="row">
                    <div className="col-md-7 col-md-push-1">
                      <div className="row">
                        <div className="col-md-10 col-md-offset-1 col-md-pull-1">
                        {/* class="form-group" */}
                          <form action="https://formspree.io/f/maylkbdv" method="POST" onSubmit={this.submitForm}>
                            <div className="form-group">
                              <input type="text" className="form-control" placeholder="Name" name="name" required></input>
                            </div>
                            <div className="form-group">
                              <input type="text" className="form-control" placeholder="Email" name="email"></input>
                            </div>
                            <div className="form-group">
                              <textarea id="message" cols="30" rows="7" className="form-control" placeholder="Message" name="message" required></textarea>
                            </div>
                            <div className="form-group">
                              {status === "SUCCESS" ? <p>Thanks!</p> : <button className="btn btn-primary btn-send-message">Send Message</button>}
                              {status === "ERROR" && <p>Ooops! There was an error.</p>}
                            </div>
                            {/* <div class="form-group">
                              <input type="submit" class="btn btn-primary btn-send-message" value="Send Message" ></input>
                            </div> */}

                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            </div>
            </div>
        </div>
        </section>
      </div>
    )
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }


}
